.adds-banners {
  padding-top: 100px;

  @media (max-width: 1430px) {
    padding-top: 80px;
  }

  @media (max-width: 910px) {
    padding-top: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
  }

  @media (max-width: 500px) {
    display: none;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__img {
    width: 100%;
    position: absolute;
    z-index: -1;
    margin: 0;
    object-fit: cover;

    @media (max-width: 1430px) {
      height: 100%;
    }
  }

  &__title {
    font-size: 30px;
    font-weight: 600;
    line-height: 39px;
    color: white;
    width: 360px;
    margin: 0 0 0 30px;

    @media (max-width: 1430px) {
      max-width: 264px;
      font-size: 22px;
      line-height: 130%;
      margin-left: 15px;
      margin-bottom: 10px;
    }
  }

  &__description {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: white;
    margin-left: 30px;
    margin-bottom: 45px;

    @media (max-width: 1430px) {
      font-size: 14px;
      line-height: 130%;
      margin-bottom: 20px;
      margin-left: 15px;
    }
  }
}

.banner {
  font-family: "Lato", sans-serif;
  width: 530px;
  height: 328px;
  margin-bottom: 50px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  @media (max-width: 1430px) {
    max-width: 300px;
    height: 254px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @media (max-width: 910px) {
    max-width: 300px;
    margin-bottom: 0;
  }
}